div {
  margin: 15px;
  font-family: serif;
}

button {
  background-color: orangered;
  color: white;
  border-radius: 30px;
  border: none;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-weight: bold;
  margin-left: 30px;
}

input::placeholder {
  font-size: 15px;
}

select {
  margin-bottom: 20px;
}

input {
  display: block;
  margin-bottom: 20px;
}

#transfer {
  background-color: grey;
  color: white;
  font-weight: bold;
}
